.notes {
    background: #181a1f;
    padding: 25px;
    margin-top: 50px;
    font-weight: 300;
    font-size: 1.3vw;
    position: relative;
}

.notes ul {
    margin-bottom: 0;
}

.notes li {
    padding-bottom: 20px;
    align-items: center;
    display: grid;
    grid-template-columns: 30px auto;
}

.notes img { 
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

@media only screen and (max-width: 768px) {
    .notes {
        font-size: 2vw;
    }
}

@media only screen and (max-width: 425px) {
    .notes {
        font-size: 4vw;
    }
}