header {
    background: #181a1f;
    text-align: center;
    font-size: 2em;
    padding: 25px 0;
    font-family: 'Montserrat', sans-serif;
}

h1 {
    font-size: 2.5vw;
    margin: 0;
}

@media only screen and (max-width: 768px) {
    h1 {
        font-size: 4vw;
    }
}

@media only screen and (max-width: 425px) {
    h1 {
        font-size: 5.2vw;
    }
}