.current {
    background: #181a1f;
    padding: 25px;
    margin-top: 50px;
    font-weight: 300;
    font-size: 1.3vw;
    position: relative;
}

.current li {
    margin-bottom: 10px;
}

@media only screen and (max-width: 768px) {
    .current {
        font-size: 2vw;
    }
}

@media only screen and (max-width: 425px) {
    .current {
        font-size: 4vw;
    }
}